import { useAppSelector } from "../redux-hoks";
export function useSelected() {
    var selectedMonth = useAppSelector(function (state) { return state.paymentState.selected.selectedMonth; });
    var selectedYear = useAppSelector(function (state) { return state.paymentState.selected.selectedYear; });
    var selectedReport = useAppSelector(function (state) {
        if (selectedMonth !== null)
            return state.paymentState[selectedYear][selectedMonth];
        return null;
    });
    return { selectedMonth: selectedMonth, selectedYear: selectedYear, selectedReport: selectedReport };
}
