var initialState = {
    month: {
        value: -1,
        error: null,
    },
    year: {
        value: -1,
        error: null,
    },
};
var partialData = {
    initialState: {
        monthAndYearInputFields: initialState,
    },
    setter: {
        setMonthAndYearInputField: function (state, action) {
            state.monthAndYearInputFields[action.payload.name] =
                action.payload.inputField;
        },
    },
};
export default partialData;
