import { selectYearsDesc, setSelected, } from "../../store/payment/paymentReducer";
import { useSelected } from "./useSelected";
import { useAppDispatch, useAppSelector } from "../redux-hoks";
import { useCallback } from "react";
export function useYearsList() {
    var dispatch = useAppDispatch();
    var years = useAppSelector(selectYearsDesc)
        .split(",")
        .filter(function (year) { return year !== ""; })
        .map(function (year) { return Number(year); });
    var selectedYear = useSelected().selectedYear;
    var onYearButtonClick = useCallback(function (year) {
        dispatch(setSelected({ selectedYear: year, selectedMonth: null }));
    }, []);
    return { years: years, selectedYear: selectedYear, onYearButtonClick: onYearButtonClick };
}
